.custom-checkbox {
    @include flex(center, flex-start);
    cursor: pointer;
    &:hover {
        color: $main-color;
    }
    input {
        position: absolute;
        opacity: 0;
        cursor: pointer;
        height: 0;
        width: 0;
    }
    &__checkMark {
        margin-right: 10px;
        height: 15px;
        width: 15px;
        border: 1px solid $txt-second-color;
        .icon-check {
            transform: scale(0);
            transition: transform 0.2s ease;
        }
    }
    input:checked~&__checkMark {
        background-color: $main-color;
        border: 1px solid $main-color;
        color: $txt-white;
    }
    input:checked~&__checkMark>.icon-check {
        transform: scale(1)
    }
}