.hero-slider {
    height: 100vh;
    max-height: 1080px;
    overflow: hidden;
    position: relative;
    margin-bottom: 50px;
    margin-top: calc(#{$header-height} * -1);
    @include tablet {
        margin-top: calc(#{$header-tablet-height} * -1);
    }
    @include tablet {
        margin-top: calc(#{$header-mobile-height} * -1);
    }
    &__item {
        @include flex(center, space-between);
        opacity: 0;
        visibility: hidden;
        pointer-events: none;
        position: absolute;
        inset: 0;
        @include tablet {
            flex-direction: column-reverse;
            padding-top: $header-tablet-height;
        }
        @include mobile {
            padding-bottom: 40px;
            padding-top: unset;
        }
        &.active {
            opacity: 1;
            visibility: visible;
            pointer-events: visible;
        }
        &__info {
            width: 58.33%;
            @include tablet {
                width: 100%;
                flex-grow: 1;
            }
            &__title,
            &__desc {
                margin-bottom: 60px;
                @include tablet {
                    margin-bottom: 15px;
                }
            }
            &__title {
                font-family: 'M PLUS Rounded 1c', sans-serif;
                font-weight: 700;
                font-size: 4.5rem;
                line-height: 6.75rem;
                overflow: hidden;
                @include mobile {
                    font-size: 2.5rem;
                    line-height: 3.75rem;
                }
            }
            &__desc {
                color: $txt-second-color;
                font-size: 1.15rem;
                line-height: 2.25rem;
                overflow: hidden;
                @include mobile {
                    font-size: 1rem;
                    line-height: 1.5rem;
                }
            }
            &__btn {
                overflow: hidden;
            }
            &__title>span,
            &__desc>span,
            &__btn button {
                display: block;
                transform: translateY(-100%);
                transition: all 0.5s ease;
            }
        }
        &.active &__info {
            &__title>span,
            &__desc>span,
            &__btn button {
                transform: translateY(0);
            }
        }
        &__img {
            flex-grow: 1;
            position: relative;
            height: 100%;
            z-index: 99;
            pointer-events: none;
            @include tablet {
                width: 100%;
                height: 60%;
                @include flex(center, center);
                flex-grow: unset;
                z-index: 97;
            }
            img {
                height: 100vh;
                position: absolute;
                bottom: 0;
                right: 0;
                transform: scale(0) rotate(65deg);
                transition: all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
                @include tablet {
                    height: 100%;
                    position: relative;
                }
            }
            .shape {
                $size: 432px;
                height: $size;
                width: $size;
                position: absolute;
                right: 0;
                top: 50%;
                border-radius: 25% 75% 74% 26% / 48% 52% 48% 52%;
                transform: translate(0, -50%);
                transition: border-radius 13s ease;
                animation: border 10s infinite;
                @include tablet {
                    $size: 250px;
                    height: $size;
                    width: $size;
                    right: 50%;
                    transform: translate(50%, -50%);
                }
            }
        }
        &.active &__img>img {
            transform: scale(1) rotate(0);
        }
    }
    &__control {
        @include flex(center, center);
        position: absolute;
        bottom: 50px;
        left: 50%;
        transform: translateX(-50%);
        &__item~&__item {
            margin-left: 10px;
        }
        &__item {
            @include flex(center, center);
            cursor: pointer;
            font-size: 2rem;
            &:hover {
                color: $main-color;
            }
            .index {
                font-size: 1.5rem;
            }
        }
    }
}

@keyframes border {
    0% {
        border-radius: 25% 75% 74% 26% / 48% 52% 48% 52%;
    }
    50% {
        border-radius: 25% 75% 25% 75% / 48% 33% 67% 52%;
    }
    100% {
        border-radius: 51% 49% 42% 58% / 79% 39% 61% 21%;
    }
}