.product-card {
    text-align: center;
    margin-bottom: 30px;
    &__image {
        padding-top: 100%;
        position: relative;
        overflow: hidden;
        margin-bottom: 30px;
        img {
            position: absolute;
            top: 0;
            left: 50%;
            transform: translateX(-50%);
            height: 100%;
            &:nth-child(1) {
                transform: translateX(-50%) scale(1);
                transition: transform .5s ease;
            }
            &:nth-child(2) {
                transform: translateX(-50%) scale(0);
            }
        }
        &:hover img {
            &:nth-child(1) {
                transform: translateX(-50%) scale(0);
            }
            &:nth-child(2) {
                transform: translateX(-50%) scale(1);
                transition: transform .5s ease;
            }
        }
    }
    &__name {
        font-size: 1.25rem;
        color: $txt-second-color;
        font-weight: normal;
        margin-bottom: 10px;
    }
    &__price {
        color: $txt-main-color;
        font-size: 1.5rem;
        font-weight: 500;
        margin-bottom: 10px;
        &__old {
            color: $txt-second-color;
            font-weight: 300;
            margin-left: 20px;
        }
    }
    &__btn {
        @include tablet {
            display: none;
        }
    }
}